import uPopup from '../../basic-component/u-popup/u-popup.vue';
import appExclusiveCoupon from '../../page-component/app-exclusive-coupon/app-exclusive-coupon.vue';
import {mapState} from "vuex";
import user from '../../../core/user.js';

export default {
    name: 'bd-coupon',
    props: {
        coupons: Array,
        theme: Object,
        guarantee_title: {
            type: String
        },
    },
    data() {
        return {
            show: false,
            newCoupons: []
        }
    },
    methods: {
        async receive(id, index) {
            uni.showLoading({
                mask: true,
                title: '领取中'
            });
            const e = await this.$request({
                url: this.$api.coupon.receive,
                data: {
                    coupon_id: id
                }
            });
            uni.hideLoading();
            if (e.code === 1) {
                uni.showModal({
                    title: '提示',
                    content: e.msg,
                    showCancel: false,
                });
            } else {
                if (e.data.rest == 0) {
                    this.$emit('change', index);
                }
                uni.showToast({
                    icon: 'none',
                    title: '领取成功',
                    duration: 1000,
                });
            }
        },
        setShow() {
            if (user.isLogin()) {
                this.show = true;
            } else {
                this.$user.getInfo();
            }

        }
    },
    computed: {
        ...mapState({
            couponImg: state => state.mallConfig.__wxapp_img.coupon,
        })
    },
    components: {
        uPopup,
        appExclusiveCoupon
    },
    watch: {
        coupons: {
            handler(data) {
                this.newCoupons = data.slice(0, 3);
            },
            deep: true,
            immediate: true
        }
    }
}
